@font-face {
  font-family: 'HelveticaNeueCyr-Light';
  src: url('../fonts/HelveticaNeueCyr-Light.eot');
  src: local('HelveticaNeueCyr-Light'), url('../fonts/HelveticaNeueCyr-Light.eot?#iefix') format('embedded-opentype'), url('../fonts/HelveticaNeueCyr-Light.woff') format('woff'), url('../fonts/HelveticaNeueCyr-Light.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'HelveticaNeueCyr-LightItalic';
  src: url('../fonts/HelveticaNeueCyr-LightItalic.eot');
  src: local('HelveticaNeueCyr-LightItalic'), url('../fonts/HelveticaNeueCyr-LightItalic.eot?#iefix') format('embedded-opentype'), url('../fonts/HelveticaNeueCyr-LightItalic.woff') format('woff'), url('../fonts/HelveticaNeueCyr-LightItalic.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'HelveticaNeueCyr-Roman';
  src: url('../fonts/HelveticaNeueCyr-Roman.eot');
  src: local('HelveticaNeueCyr-Roman'), url('../fonts/HelveticaNeueCyr-Roman.eot?#iefix') format('embedded-opentype'), url('../fonts/HelveticaNeueCyr-Roman.woff') format('woff'), url('../fonts/HelveticaNeueCyr-Roman.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'HelveticaNeueCyr-Medium';
  src: url('../fonts/HelveticaNeueCyr-Medium.eot');
  src: local('HelveticaNeueCyr-Medium'), url('../fonts/HelveticaNeueCyr-Medium.eot?#iefix') format('embedded-opentype'), url('../fonts/HelveticaNeueCyr-Medium.woff') format('woff'), url('../fonts/HelveticaNeueCyr-Medium.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'HelveticaNeueCyr-Bold';
  src: url('HelveticaNeueCyr-Bold.eot');
  src: local('HelveticaNeueCyr-Bold'), url('HelveticaNeueCyr-Bold.eot?#iefix') format('embedded-opentype'), url('HelveticaNeueCyr-Bold.woff') format('woff'), url('HelveticaNeueCyr-Bold.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: "HelveticaNeueCyr-Roman", sans-serif;
  font-size: 16px;
}

.section-title {
  text-align: center;
  color: #171717;
  font-size: 35px;
  font-weight: 500;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.button,
#form-submit {
  display: inline-block;
  text-decoration: none;
  box-shadow: 0 2px 20px 1px rgba(0, 0, 0, 0.18);
  border-radius: 36px;
  padding: 25px 35px;
  background-color: #90ee90;
  color: #ffffff;
  font-size: 22px;
  font-family: 'HelveticaNeueCyr-Medium';
  transition: all 0.5s ease;
}

.button:hover,
#form-submit:hover {
  background-color: #64e764;
}

.logo {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.logo__image {
  margin-right: 15px;
}

.logo__text {
  color: #90ee90;
  letter-spacing: 0.2em;
  font-family: 'HelveticaNeueCyr-LightItalic';
  font-size: 33px;
}

.logo span {
  display: block;
  color: #717172;
  font-size: 16px;
  font-weight: 300;
  line-height: 36px;
  letter-spacing: 0.12em;
}

.header {
  padding-top: 21px;
  padding-bottom: 35px;
}

.header-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.header-navigation {
  margin-bottom: 45px;
}

.header-navigation ul {
  display: flex;
  flex-wrap: wrap;
}

.header-navigation ul li {
  margin: 0 20px;
}

.header-navigation ul li a {
  color: #000000;
  font-size: 18px;
  text-decoration: none;
  transition: all 0.5s ease;
}

.header-navigation ul li a:hover,
.header-navigation ul li a:active {
  color: #00bbb6;
  text-decoration: underline;
}

.header-info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.header-info .header-tel {
  padding-left: 31px;
  background: url(../img/whatsapp-icon.png) no-repeat 0 center;
}

.header-info .header-tel span {
  font-size: 22px;
  font-family: 'HelveticaNeueCyr-Medium';
}

.header-info .header-tel a {
  color: #000000;
  text-decoration: none;
}

.header-info .button {
  padding: 10px 0;
  padding-left: 52px;
  padding-right: 22px;
  color: #00bbb6;
  border: 1px solid #00bbb6;
  background: url(../img/phone-icon.png) no-repeat 20px center;
  background-color: transparent;
  box-shadow: none;
  font-size: 16px;
  transition: all 0.5s ease;
}

.header-info .button:hover {
  color: #ffffff;
  background-color: #00bbb6;
}

.header-info .header-socials a {
  margin-left: 16px;
}

.header-info .header-socials a img:hover {
  transform: scale(1.1);
}

.hero {
  color: #ffffff;
  padding-top: 60px;
  padding-bottom: 53px;
  background: #87d5fb url(../img/hero-bg.jpg) no-repeat center top / cover;
}

.hero .slide {
  padding: 63px 38px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}

.hero .slide__title {
  font-size: 53px;
  line-height: 65px;
  margin-bottom: 23px;
  font-family: 'HelveticaNeueCyr-Light';
}

.hero .slide__title b {
  font-family: 'HelveticaNeueCyr-Medium';
}

.hero .slide__text {
  font-size: 17px;
  line-height: 21px;
  margin-bottom: 58px;
}

.hero .slide .button {
  text-align: center;
}

.price {
  padding-top: 95px;
  padding-bottom: 100px;
}

.price .section-title {
  margin-bottom: 35px;
}

.price .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -12.5px;
  margin-right: -12.5px;
  justify-content: center;
  text-align: center;
}

.price .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 12.5px;
  margin-right: 12.5px;
  width: calc(33.333333333333336% - 25px);
  margin-bottom: 9px;
  border-radius: 10px;
}

.price .items .item .img {
  position: relative;
}

.price .items .item .img img {
  width: 100%;
  height: auto;
}

.price .items .item .text {
  border-radius: 10px;
  transition: all 0.5s ease;
  opacity: 0;
  box-sizing: border-box;
  color: #ffffff;
  position: absolute;
  width: 100%;
  padding: 18px 9px;
  bottom: 1px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.7);
}

.price .items .item .text span {
  font-size: 24px;
  font-family: 'HelveticaNeueCyr-Medium';
  color: #90ee90;
}

.price .items .item:hover .text {
  opacity: 1;
}

.feedback {
  padding-top: 55px;
  padding-bottom: 44px;
  color: #ffffff;
  background: #00bbb6 url(../img/feedback-background.png) no-repeat center top / cover;
}

.feedback-wrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -12.5px;
  margin-right: -12.5px;
}

.feedback-content {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 12.5px;
  margin-right: 12.5px;
  width: calc(58.333333333333336% - 25px);
}

.feedback-promo {
  margin-bottom: 52px;
  font-size: 36px;
  font-family: 'HelveticaNeueCyr-Light';
  line-height: 52px;
}

.feedback-promo b {
  font-size: 38px;
  font-family: 'HelveticaNeueCyr-Roman';
}

.feedback-promo span {
  font-size: 40px;
  font-family: 'HelveticaNeueCyr-Roman';
}

.feedback-form input {
  box-sizing: border-box;
  border: none;
  width: 100%;
  margin-bottom: 13px;
  padding: 25px 37px;
  border-radius: 33px;
  background-color: #ffffff;
  color: #dcdbdb;
  font-weight: 300;
}

.feedback-form .button {
  width: 100%;
  margin-top: 30px;
}

.advantages {
  padding-top: 95px;
  padding-bottom: 6px;
}

.advantages .section-title {
  margin-bottom: 61px;
}

.advantages .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -12.5px;
  margin-right: -12.5px;
  justify-content: center;
}

.advantages .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 12.5px;
  margin-right: 12.5px;
  width: calc(33.333333333333336% - 25px);
  box-shadow: 0 2px 8px 1px rgba(0, 0, 0, 0.18);
  border-radius: 10px;
  margin-bottom: 22px;
  padding-top: 23px;
  padding-left: 17px;
  padding-bottom: 27px;
  padding-right: 16px;
}

.advantages .items .item__title {
  background-image: url(../img/advantages-icon.png);
  background-repeat: no-repeat;
  background-position: top right;
  font-size: 24px;
  color: #90ee90;
  font-weight: 500;
  padding-right: 150px;
  margin-bottom: 25px;
}

.advantages .items .item__text {
  color: #9f9e9e;
  font-size: 14px;
  font-weight: 300;
  line-height: 14px;
}

.about {
  padding-top: 95px;
  padding-bottom: 94px;
}

.about .section-title {
  margin-bottom: 31px;
}

.about-text {
  color: #4f4f4f;
  line-height: 22px;
}

.footer {
  padding-top: 43px;
  padding-bottom: 54px;
  background-color: #2d2d2d;
}

.footer-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.footer .logo span {
  color: #ffffff;
}

.footer-menu ul {
  max-height: 120px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.footer-menu ul li {
  margin-bottom: 20px;
}

.footer-menu ul li a {
  color: #ffffff;
  text-decoration: none;
  transition: all 0.5s ease;
}

.footer-menu ul li a:hover,
.footer-menu ul li a:active {
  color: #00bbb6;
  text-decoration: underline;
}

.footer-info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.footer-info .footer-tel {
  margin-bottom: 17px;
  padding-left: 31px;
  background: url(../img/whatsapp-icon.png) no-repeat 0 center;
}

.footer-info .footer-tel span {
  font-size: 22px;
  font-family: 'HelveticaNeueCyr-Medium';
}

.footer-info .footer-tel a {
  color: #fff;
  text-decoration: none;
}

.footer-info .button {
  padding: 10px 32px;
  color: #00bbb6;
  border: 1px solid #00bbb6;
  background-color: transparent;
  box-shadow: none;
  margin-bottom: 17px;
  font-size: 16px;
  transition: all 0.5s ease;
}

.footer-info .button:hover {
  color: #ffffff;
  background-color: #00bbb6;
}

.footer-info .footer-socials a {
  margin-left: 13px;
}

.footer-info .footer-socials a img:hover {
  transform: scale(1.1);
}

.slick-dots {
  bottom: 20px;
}

.slick-dots li button::before {
  font-size: 15px;
  color: #fff;
  opacity: 1;
}

.slick-dots li.slick-active button:before {
  color: #90ee90;
  opacity: 1;
}

@media screen and (max-width: 1152px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }

  .header-wrap {
    justify-content: center;
  }

  .header .logo {
    margin-bottom: 20px;
  }

  .footer-menu ul {
    max-height: unset;
  }
}

@media screen and (max-width: 992px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .header-navigation {
    margin-bottom: 0px;
  }

  .header-navigation ul li {
    margin-bottom: 20px;
  }

  .price .items .item {
    width: calc(50% - 25px);
  }

  .feedback-content {
    width: calc(75% - 25px);
  }

  .advantages .items .item {
    width: calc(41.66666666666667% - 25px);
  }
}

@media screen and (max-width: 720px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }

  .hero .slide__title {
    font-size: 37px;
  }

  .hero .slide__title {
    line-height: 40px;
  }

  .price .items .item {
    width: calc(66.66666666666667% - 25px);
  }

  .feedback-content {
    width: calc(100% - 25px);
  }

  .advantages .items .item {
    width: calc(58.333333333333336% - 25px);
  }

  .footer-wrap {
    justify-content: center;
  }

  .footer-menu {
    display: none;
  }

  .footer-info .footer-socials a {
    margin-left: 0;
  }
}

@media screen and (max-width: 576px) {
  .header-info {
    justify-content: center;
  }

  .header-info .header-tel {
    margin-bottom: 10px;
  }

  .header-info .button {
    margin-bottom: 10px;
  }

  .advantages .items .item {
    width: calc(100% - 25px);
  }
}